
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'

export default {
  components: {
    Group,
    Cell,
    Tab,
    TabItem,
    Sticky
  },
  data () {
    return {
      tabs: ['今天排班', '明天排班', '后天排班'],
      TabSelected: '今天排班',
      index: 0
    }
  },
  methods: {
    handler (item) {
      switch (item) {
        case '今天排班' : this.$router.push('/steward/todayshift')
          break;
        case '明天排班' : this.$router.push('/steward/tomoshift')
          break;
        case '后天排班' : this.$router.push('/steward/aftertomoshift')
          break;
        default:
          break;
      }
    }
  }
}



